}

.checkbox .label-area {
  color: #2490ef;
}

.form-section.card-section {
  border-bottom: 8px solid #b4f5ff;
}

.section-body [data-fieldname=items] * {
  font-size: 11px;
}

.section-body .form-grid .grid-heading-row {
  background-color: #24b0c7;
  color: #fff;
}

.form-control,
.form-control:focus {
